import { Circles } from  'react-loader-spinner';

const Loader = () => {
    return (
        <Circles
        height="80"
        width="80"
        color="#111827"
        ariaLabel="circles-loading"
        wrapperStyle={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
        }}
        wrapperClass=""
        visible={true}
        />
    )
}

export default Loader;